.block-practice-areas {
  width: 100%;
  display: flex;
}
.block-practice-areas__inner {
  width: 100%;
  padding: 70px 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
@media (min-width: 768px) {
  .block-practice-areas__inner {
    padding: 70px 0;
    flex-flow: row wrap;
  }
}
@media (min-width: 1441px) {
  .block-practice-areas__inner {
    padding: 120px 0;
  }
}
.block-practice-areas__inner .heading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
@media (min-width: 1441px) {
  .block-practice-areas__inner .heading {
    flex-flow: row wrap;
  }
}
.block-practice-areas__inner .heading .block-title {
  margin-bottom: 30px;
  color: rgb(var(--primary-color));
}
@media (min-width: 1441px) {
  .block-practice-areas__inner .heading .block-title {
    margin-bottom: 0;
    flex: 0 0 40%;
    border-right: 2px solid rgba(0, 0, 0, 0.2);
    text-align: left;
  }
}
.block-practice-areas__inner .heading .block-title span,
.block-practice-areas__inner .heading .block-title strong {
  color: rgb(var(--secondary-color));
}
@media (min-width: 1441px) {
  .block-practice-areas__inner .heading-text {
    padding: 30px;
    flex: 0 0 60%;
  }
  .block-practice-areas__inner .heading-text p {
    text-align: left;
  }
}
.block-practice-areas__inner .heading-text p {
  font-family: var(--poppins);
  font-size: 2rem;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: 0.5px;
  color: rgb(var(--primary-color));
}
.block-practice-areas__inner .practice-areas {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
@media (min-width: 768px) {
  .block-practice-areas__inner .practice-areas {
    flex-flow: row wrap;
  }
}
.block-practice-areas__inner .practice-areas .pa {
  width: 100%;
  height: 232px;
  position: relative;
  overflow: hidden;
  display: flex;
}
@media (min-width: 768px) {
  .block-practice-areas__inner .practice-areas .pa {
    height: 260px;
    min-height: fit-content;
    flex: 0 0 calc(50% - 30px);
  }
}
@media (min-width: 1441px) {
  .block-practice-areas__inner .practice-areas .pa {
    height: 312px;
    flex: 0 0 calc(33.333% - 30px);
  }
}
.block-practice-areas__inner .practice-areas .pa:hover .bg-img {
  transform: scale(1.2);
}
.block-practice-areas__inner .practice-areas .pa:hover .pa-wrapper {
  height: 100%;
  top: 0;
  background-color: rgba(var(--secondary-color), 0.8);
  opacity: 1;
}
.block-practice-areas__inner .practice-areas .pa:hover .content-wrapper {
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.block-practice-areas__inner .practice-areas .pa:hover .title {
  bottom: -100px;
  opacity: 0;
}
.block-practice-areas__inner .practice-areas .pa .bg-img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  object-fit: cover;
  object-position: center center;
  z-index: 0;
  transition: 0.3s linear;
}
.block-practice-areas__inner .practice-areas .pa-wrapper {
  width: 100%;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  opacity: 0;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s ease 0.15s;
}
@media (min-width: 1441px) {
  .block-practice-areas__inner .practice-areas .pa-wrapper {
    padding: 70px 40px;
  }
}
.block-practice-areas__inner .practice-areas .pa .content-wrapper {
  width: 100%;
  margin: 0;
  padding: 15px 10px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease 0s;
}
.block-practice-areas__inner .practice-areas .pa .content-wrapper .heading {
  margin-bottom: 20px;
  position: relative;
  font-size: 18px;
  line-height: normal;
  font-weight: 500;
  color: #fff;
  text-align: center;
}
.block-practice-areas__inner .practice-areas .pa .content-wrapper .content {
  width: 100%;
  margin-bottom: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 2rem;
  font-weight: 400;
  text-align: center;
  color: #fff;
}
@media (min-width: 768px) {
  .block-practice-areas__inner .practice-areas .pa .content-wrapper .content {
    white-space: unset;
    overflow: auto;
    text-overflow: unset;
  }
}
.block-practice-areas__inner .practice-areas .pa .content-wrapper .link {
  display: flex;
  align-items: center;
  font-size: 2rem;
  font-family: var(--poppins);
  color: #fff;
}
.block-practice-areas__inner .practice-areas .pa .content-wrapper .link span {
  margin-left: 6px;
  font-size: 16px;
  line-height: 34px;
  font-weight: 600;
}
.block-practice-areas__inner .practice-areas .pa .title {
  width: 100%;
  padding: 15px 30px;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 1;
  background-color: rgb(var(--primary-color));
  text-align: center;
  transition: all 0.3s ease 0s;
}
.block-practice-areas__inner .practice-areas .pa .title a {
  font-size: 20px;
  font-weight: 500;
  color: #fff;
}